.xw-image img {
  position: absolute; }

.xw-image .image {
  width: 45%;
  -webkit-transform: rotate(10deg);
  -ms-transform: rotate(10deg);
  transform: rotate(10deg); }

.xw-image .background {
  width: 48%;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg); }



